/* Import a modern font */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

.add-bank-account {
    margin-top: 20px;
    padding: 20px;
}

.add-bank-account h3 {
    margin-top: 0;
    margin-bottom: 20px;
    font-weight: 700;
}

.add-bank-account form {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* Safari-compatible select dropdown styling */
.add-bank-account select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 8px 12px;
    font-size: 14px;
    color: #333;
    cursor: pointer;
    background-image: url('data:image/svg+xml;charset=US-ASCII,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 4 5"><path fill="%23666" d="M2 0L0 2h4zm0 5L0 3h4z"/></svg>');
    background-repeat: no-repeat;
    background-position: right 8px center;
    background-size: 12px;
    padding-right: 30px;
}

.add-bank-account select:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

.add-bank-account select option {
    padding: 8px;
    background-color: white;
    color: #333;
}

.add-bank-account select option:hover {
    background-color: #f0f0f0;
}

.add-bank-account form button:not(date-picker button) {
    width: 100%;
    padding: 12px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 1.1rem;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.add-bank-account form button:not(date-picker button):hover {
    background-color: #0056b3;
    transform: translateY(-2px);
}

.add-bank-account form button:active {
    background-color: #004085;
    transform: translateY(0);
}

.add-bank-account form button:disabled {
    background-color: #94c3f0;
    cursor: not-allowed;
    transform: none;
    position: relative;
    overflow: hidden;
}

.add-bank-account form button:disabled::after {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    top: 50%;
    left: 50%;
    margin: -10px 0 0 -10px;
    border: 2px solid #fff;
    border-top-color: transparent;
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.add-bank-account form button:disabled[data-loading="true"] {
    color: transparent;
}

.add-bank-account form button:disabled[data-loading="true"]::after {
    opacity: 1;
}

@keyframes spin {
    to { transform: rotate(360deg); }
}

