import { Component, h, State, Prop, Event, EventEmitter, Element } from '@stencil/core';
import { STATES, COUNTRY, CountryAndPhoneCodes } from '../../utils/state';
import { addBankAccount as addBankAccountService } from '../../utils/addBankService';
import { Environment } from '../../config';
import { initWalletEvents, trackAddBankSuccess, trackAddBankError, trackAddBankCancelled, trackValidationError } from '../../utils/walletEvents';
import { AccountStatus, BankAccountType } from "../../interfaces/common";
import { isValidPOBoxAddess } from '../../utils/validations';
import { oscilarService } from '../../utils/oscilarService';

@Component({
  tag: 'add-bank-account',
  styleUrl: 'addbankaccount.module.css',
  shadow: false,
})



export class AddBankAccount {
  @Event({ eventName: 'goToPaymentSelector', bubbles: true, composed: true })
  goToPaymentSelector: EventEmitter<boolean>;
  @Event({ eventName: 'addressValidation', bubbles: true, composed: true })
  addressValidation: EventEmitter<string>;
  private  errorBannerRef: HTMLElement | null = null;

  // Helper function to conditionally log only in development
  private devLog = (message: string, ...args: any[]) => {
    if (this.environment === Environment.LOCALDEVELOPMENT || this.environment === Environment.STAGING) {
      console.log(message, ...args);
    }
  };
  formattedMobileNumber: string;
  @Prop() operationsToken: string;
  @Prop() userScopedAccessToken: string;
  @Prop() environment: Environment = Environment.PRODUCTION; // Environment parameter with production default

  @Event() bankAccountAdded: EventEmitter<string>;

  @State() accountHolderName: string = '';
  @State() accountNumber: string = '';
  @State() routingNumber: string = '';
  @State() bankAccountType: BankAccountType = BankAccountType.Checking;
  @State() dateofBirth: string = '';
  @State() paymentAccountNickname: string = '';
  @State() firstName: string = '';
  @State() lastName: string = '';
  @State() address1: string = '';
  @State() address2: string = '';
  @State() city: string = '';
  @State() state: string = '';
  @State() country: string = '';
  @State() zipCode: string = '';
  @State() mobilePhoneNumber: string = '';
  @State() selectedPhoneCode: string = '';
  @State() isPhoneCodeDropdownOpen: boolean = false;
  @State() emailAddress: string = '';
  @State() apiErrors: { [key: string]: string[] } = {};
  @State() isLoading: boolean = false;
  @State() mfaPopUp: boolean = false;
  @State() PaymentAccountRequestPayload: any;
  @State() mfaResponsePayload: any;
  @State() brokenRules: string[] = [];
  @State() isAddressSuggestion: boolean = false;
  @State() isVerifying: boolean = false;
  @State() errors: {
    accountHolderName: string;
    accountNumber: string;
    routingNumber: string;
    bankAccountType: string;
    firstName: string;
    lastName: string;
    address1: string;
    address2: string;
    dateofBirth: string;
    paymentAccountNickname: string;
    city: string;
    state: string;
    country: string;
    zipCode: string;
    mobilePhoneNumber: string;
    emailAddress: string;
    selectedPhoneCode: string;
  } = {
      accountHolderName: '',
      accountNumber: '',
      routingNumber: '',
      bankAccountType: '',
      firstName: '',
      lastName: '',
      address1: '',
      city: '',
      state: '',
      country: '',
      zipCode: '',
      address2: '',
      dateofBirth: '',
      paymentAccountNickname: '',
      mobilePhoneNumber: '',
      emailAddress: '',
      selectedPhoneCode: '',
    };
  
  @State() isAddressResubmission: boolean = false;
  @Element() el: HTMLElement;
  private dropdownWrapper!: HTMLElement;

  componentWillLoad() {
    // Initialize event tracking
    initWalletEvents('AddBankAccount', this.environment);
  }

  handleInputChange(field: string, value: string) {
    this[field] = value;
    // Clear error when user starts typing
    if (this.errors[field]) {
      this.errors = { ...this.errors, [field]: '' };
    }
  }

  handleMobileNumberChange(event: Event) {
    const input = event.target as HTMLInputElement;

    let digits = '';

    if (this.country === 'US') {
      digits = input.value.replace(/\D/g, '').slice(0, 10);
    } else {
      digits = input.value.replace(/\D/g, '');
    }

    let formatted = '';

    if (this.country === 'US') {
      if (digits.length > 6) {
        formatted = `(${digits.slice(0, 3)}) ${digits.slice(3, 6)}-${digits.slice(6)}`;
      } else if (digits.length > 3) {
        formatted = `(${digits.slice(0, 3)}) ${digits.slice(3)}`;
      } else if (digits.length > 0) {
        formatted = `(${digits}`;
      }
    } else {
      formatted = digits;
    }

    input.value = formatted;

    this.mobilePhoneNumber = formatted;

    if (this.errors[input.id]) {
      this.errors = { ...this.errors, [input.id]: '' };
    }
  }
  private showErrorBanner(messages: string[]) {
    setTimeout(() => {
      if (messages?.length > 0 && this.errorBannerRef) {
        this.devLog("showErrorBanner");
        const top = this.errorBannerRef.offsetTop;
        this.scrollToView(top);
      }
    }, 300);
  }

  private scrollToView(topValue: number) {
    window.scrollTo({
      top: topValue - 20,
      behavior: 'smooth',
    });
  }
  handleStateChange(event: Event) {
    const target = event.target as HTMLSelectElement;
    const selectedState = STATES(this.country).find(state => state.code === target.value);

    if (selectedState) {
      this.state = selectedState.code;
      // Clear state error when valid state is selected
      this.errors = { ...this.errors, state: '' };
    } else {
      // Handle case when user goes back to "Select state/province" (empty value)
      this.state = '';
      // Clear the error to allow validation to trigger again on form submit
      this.errors = { ...this.errors, state: '' };
    }
  }

  isCountryUS(): boolean {
    return this.country === 'US';
  }

  isCountryUSorCanada(): boolean {
    return this.country === 'US' || this.country === 'CA';
  }

  getProvinceOrStateCode(): string {
    // If country is US or Canada, use the state value from picklist
    // For all other countries, pass an empty value
    return (this.country === 'US' || this.country === 'CA') ? this.state : '';
  }

  getDefaultState(): string {
    // below LIne is commented as part of SonarQube improvements
    //return this.isCountryUSorCanada() ? '' : '';
    return '';
  }

  handleCountryChange(event: Event) {
    const target = event.target as HTMLSelectElement;
    const selectedCountry = COUNTRY.find(country => country.code === target.value);

    if (selectedCountry) {
      this.country = selectedCountry.code;
      // Clear country & phoneCode error (provided that for selected country phone code has to be there)when valid country is selected
      this.errors = { ...this.errors, country: '', selectedPhoneCode: '', };
      // Reset state when country changes
      this.state = '';
      if (this.country === 'US') {
        this.mobilePhoneNumber = '';
        this.selectedPhoneCode = '';
      }
      if (this.country !== 'US') {
        const phoneCodeMap = new Map(CountryAndPhoneCodes.map(c => [c.Id, c.PhoneCode]));
        this.selectedPhoneCode = phoneCodeMap.get(this.country) || '';
        this.mobilePhoneNumber = '';
      }
    } else {
      // Handle case when user goes back to "Select country" (empty value)
      this.country = '';
      this.selectedPhoneCode = '';
      // Clear the error to allow validation to trigger again on form submit
      this.errors = { ...this.errors, country: '', selectedPhoneCode: '', };
      this.state = '';
    }
  }

  handlePhoneCodeChange(event: Event) {
    const target = event.target as HTMLSelectElement;
    if (this.errors[target.id]) {
      this.errors = { ...this.errors, [target.id]: '' };
    }
    this.selectedPhoneCode = target.value;
  }

 handleBirthDateChange(value: string) {
  const numericValue = value.replace(/\D/g, "");
    // Format the date with slash after 2 digits
    let formattedValue = "";
    if (numericValue.length > 0) {
      formattedValue += numericValue.slice(0, 2);
      if (numericValue.length > 2) {
        formattedValue += "/" + numericValue.slice(2, 4);
        if (numericValue.length > 4) {
          formattedValue += "/" + numericValue.slice(4, 8);
        }
      }
    }

    this.dateofBirth = formattedValue;

    // Clear error when user starts typing
    if (this.errors.dateofBirth) {
      this.errors = { ...this.errors, dateofBirth: '' };
    }
  }
  validateMobilePhoneNumber(): { [key: string]: string } {
    const errors: { [key: string]: string } = {};
    const phone = this.mobilePhoneNumber.trim();

    if (phone.length === 0) {
      errors.mobilePhoneNumber = 'Mobile Phone Number is required.';
    } else {
      let pattern: RegExp;

      if (this.country === 'US') {
        pattern = /^\([1-9]\d{2}\)\s?\d{3}-\d{4}$/g;
      } else {
        pattern = /^\d*$/g;
      }

      if (!pattern.test(phone)) {
        errors.mobilePhoneNumber = 'Invalid Mobile Phone Number';
      }
    }

    return errors;
  }
  validateEmailAddress(): { [key: string]: string } {
    const errors: { [key: string]: string } = {};

    if (!this.emailAddress.trim()) {
      errors.emailAddress = 'E-Mail Address is required.';
    } else if (this.emailAddress.trim().length > 60) {
      errors.emailAddress = 'E-Mail Address must not exceed 60 characters.';
    } else {
      const re = /^([\w-.]+@([\w-]+\.)+[\w-]{2,4})$/;
      if (!re.test(this.emailAddress.trim())) {
        errors.emailAddress = 'Please enter a valid E-Mail Address.';
      }
    }

    return errors;
  }
  validateSelectedPhoneCode(): { [key: string]: string } {
    const errors: { [key: string]: string } = {};
    if (!this.isCountryUS() && !this.selectedPhoneCode.trim()) {
      errors.selectedPhoneCode = 'Country Code is required';
    }
    return errors;
  }
  togglePhoneCodeDropdown = (event: MouseEvent) => {
    event.stopPropagation(); 
    this.isPhoneCodeDropdownOpen = !this.isPhoneCodeDropdownOpen;
    this.errors = { ...this.errors, selectedPhoneCode: '' };
    if (this.isPhoneCodeDropdownOpen) {
      document.addEventListener('click', this.handleOutsideClick);
      document.addEventListener('keydown', this.handleEscapePress);
    } else {
      this.removeDropdownListeners();
    }
  };

  selectPhoneCode = (code: string) => {
    this.selectedPhoneCode = code;
    this.isPhoneCodeDropdownOpen = false;
    this.errors = { ...this.errors, selectedPhoneCode: '' };
    this.removeDropdownListeners();
  };

  handleOutsideClick = (event: MouseEvent) => {
    if (this.dropdownWrapper && !this.dropdownWrapper.contains(event.target as Node)) {
      this.isPhoneCodeDropdownOpen = false;
      this.removeDropdownListeners();
    }
  };

  handleEscapePress = (event: KeyboardEvent) => {
    if (event.key === 'Escape') {
      this.isPhoneCodeDropdownOpen = false;
      this.removeDropdownListeners();
    }
  };

  private removeDropdownListeners(): void {
    document.removeEventListener('click', this.handleOutsideClick);
    document.removeEventListener('keydown', this.handleEscapePress);
  };

  handleClosePopup = () => {
    this.mfaPopUp = false;
  };


  private handleMfaError(messages: string[]) {
    if (messages?.length > 0) {
      this.brokenRules = [messages[0]];
      this.showErrorBanner(messages);
    }
  }
  private validateAccountFields(): { [key: string]: string } {
    const errors: { [key: string]: string } = {};

    if (!this.accountHolderName.trim()) {
      errors.accountHolderName = 'Account Holder Name is required.';
    }

    if (!this.accountNumber.trim()) {
      errors.accountNumber = 'Account Number is required.';
    } else if (!/^\d+$/.test(this.accountNumber)) {
      errors.accountNumber = 'Account Number must contain only numbers.';
    }

    if (!this.routingNumber.trim()) {
      errors.routingNumber = 'Routing Number is required.';
    } else if (!/^\d{9}$/.test(this.routingNumber)) {
      errors.routingNumber = 'Routing Number must be 9 digits.';
    }

    return errors;
  }

  private validatePersonalFields(): { [key: string]: string } {
    const errors: { [key: string]: string } = {};

    if (!this.firstName.trim()) {
      errors.firstName = 'First Name is required.';
    }
    if (!this.lastName.trim()) {
      errors.lastName = 'Last Name is required.';
    }
    if (!this.paymentAccountNickname.trim()) {
      errors.paymentAccountNickname = 'Payment Account Nickname is required.';
    }

    return errors;
  }

  private validateDateOfBirth(): { [key: string]: string } {
    const errors: { [key: string]: string } = {};

    if (!this.dateofBirth.trim()) {
      errors.dateofBirth = 'Date of Birth is required.';
      return errors;
    }

    const re = /^(0[1-9]|1[012])[- /.](0[1-9]|[12]\d|3[01])[- /.](19|20)\d\d$/g;
    if (!re.test(this.dateofBirth)) {
      errors.dateofBirth = 'Please enter a valid date in MM/DD/YYYY format.';
      return errors;
    }

    const enteredDate = new Date(this.dateofBirth);
    const minAgeDate = new Date();
    minAgeDate.setFullYear(minAgeDate.getFullYear() - 18);
    if (enteredDate > minAgeDate) {
      errors.dateofBirth = 'Must be at least 18 years of age.';
    }

    return errors;
  }

  private validateAddressFields(): { [key: string]: string } {
    const errors: { [key: string]: string } = {};


    if (!this.address1.trim()) {
      errors.address1 = 'Address Line 1 is required.';
    } else if ( isValidPOBoxAddess(this.address1)) {
      errors.address1 = 'PO Boxes are not allowed. Please enter a valid Address Line 1.';
    }

    if (
      this.address2.trim().length > 0 &&
       isValidPOBoxAddess(this.address2)
    ) {
      errors.address2 = 'PO Boxes are not allowed. Please enter a valid Address Line 2.';
    }


    if (!this.city.trim()) {
      errors.city = 'City is required.';
    }

    if (this.isCountryUSorCanada() && !this.state.trim()) {
      errors.state = 'State is required.';
    }

    if (!this.country.trim()) {
      errors.country = 'Country is required.';
    }

    if (!this.zipCode.trim()) {
      errors.zipCode = 'ZIP/Postal Code is required.';
    }

    return errors;
  }

  validate(): boolean {
    this.isLoading = true;
    const newErrors = {
      accountHolderName: '',
      accountNumber: '',
      routingNumber: '',
      bankAccountType: '',
      firstName: '',
      lastName: '',
      address1: '',
      city: '',
      state: '',
      country: '',
      zipCode: '',
      address2: '',
      dateofBirth: '',
      paymentAccountNickname: '',
      mobilePhoneNumber: '',
      emailAddress: '',
      selectedPhoneCode: '',
      ...this.validateAccountFields(),
      ...this.validatePersonalFields(),
      ...this.validateDateOfBirth(),
      ...this.validateAddressFields(),
      ...this.validateMobilePhoneNumber(),
      ...this.validateEmailAddress(),
      ...this.validateSelectedPhoneCode(),
    };

    this.errors = newErrors;

    const errorMessages = Object.values(newErrors).filter(error => error !== '');
    if (errorMessages.length > 0) {
      trackValidationError('bank', errorMessages);
    }

    this.scrollToFirstError();

    return !Object.values(newErrors).some(error => error !== '');
  }

  private scrollToFirstError() {
    // Wait for the next frame to ensure the DOM has updated
    requestAnimationFrame(() => {
      // Search the entire document for error elements
      const errorElement = this.el.querySelector('.validation-error');

      if (errorElement) {
        errorElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
      }
    });
  }

  async handleSubmit(event: Event) {
    event.preventDefault();
    this.apiErrors = {}; // Clear previous API errors
    this.brokenRules = [];
    this.isVerifying = true;
    this.isLoading = true;

    if (!this.validate()) {
      this.isLoading = false;
      this.isVerifying = false;
      return;
    }

      this.formattedMobileNumber = this.selectedPhoneCode
        ? '+' + this.selectedPhoneCode.trim() + this.mobilePhoneNumber.trim()
        : this.mobilePhoneNumber;

    try {
      const payload = {
        walletOwnerIdentifiers: {
          customer: {
            customerInformation: {
              firstName: this.firstName,
              lastName: this.lastName
            }
          }
        },
        accountReferenceId: `bank-${Date.now()}`,
        accountNumber: this.accountNumber,
        payorInformation: {
          firstName: this.firstName,
          lastName: this.lastName,
          paymentAccountNickname: this.paymentAccountNickname,
          validateAddress: !this.isAddressResubmission, // true for first submission, false for resubmission
          dateofBirth: this.formatDateForAPI(this.dateofBirth), // Format as YYYY-MM-DD
          contactInformation: {
            emailAddress: this.emailAddress, // Add fallback for missing properties
            phoneNumber: this.formattedMobileNumber,
          },
          identityVerificationInformation: null
        },
        billingAddress: {
          addressLine1: this.address1,
          addressLine2: this.address2 || '',
          city: this.city,
          provinceOrStateCode: this.getProvinceOrStateCode(),
          countryCode: this.country || 'US',
          postalCode: this.zipCode,
        },
        routingNumber: this.routingNumber,
        bankAccountType: this.bankAccountType,
      };
      this.PaymentAccountRequestPayload = payload;

    // Commit device data to Oscilar before making the API call
      oscilarService.commit();

    const response = await addBankAccountService(this.operationsToken, this.userScopedAccessToken, payload, this.environment);
      if (response.success && response?.data?.paymentInstrument?.paymentInstrumentToken) {
        this.handleSuccess(response);
      }
      else if (response.success && response?.data?.AccountStatus) {
        if (response?.data?.AccountStatus === AccountStatus.AddressSuggested) {
          this.isAddressSuggestion = true;
        }
        else {
          this.isAddressSuggestion = false;
        }
        // Initialize brokenRules to empty array if not present
        this.brokenRules = response?.data?.BrokenRules || [];
        if (this.brokenRules.length > 0) {
          this.devLog("response.success");
          this.showErrorBanner(this.brokenRules);
        }

        const status = response?.data?.AccountStatus;
        switch (status) {
          case AccountStatus.AddressSuggested:
            this.handleAddressValidation(response);
            break;
          case AccountStatus.MFAPending:
            this.mfaPopUp = true;
          this.mfaResponsePayload = response;
            break;
          case AccountStatus.AddressValidationError:
          case AccountStatus.AddressValidationUnknownStatus:
            this.handleValidationError(response);
            break;
          case AccountStatus.Saved:
            this.handleSuccess(response);
            break;
          default:
            this.handleUnknownError(response);
            break;
        }
      }
      else {
        this.handleResponseError(response);
      }
    } catch (error: any) {
      this.isLoading = false;

      // Track unexpected error
      trackAddBankError(error.message || 'Unexpected error occurred');

      // Handle unexpected errors
      console.error('Error in handleSubmit:', error);
      this.apiErrors = {
        General: [error.message || 'An unexpected error occurred. Please try again.']
      };
    } finally {
      this.isVerifying = false;
      this.isLoading = false;
    }
  }
  private formatDateForAPI(dateString: string): string {
    if (!dateString || dateString.trim() === '') {
      return '';
    }

    // Parse MM/DD/YYYY format
    const parts = dateString.split('/');
    if (parts.length !== 3) {
      return dateString; // Return original if not in expected format
    }

    const month = parts[0].padStart(2, '0');
    const day = parts[1].padStart(2, '0');
    const year = parts[2];

    // Return in YYYY-MM-DD format
    return `${year}-${month}-${day}`;
  }
  private handleSuccess(response: any) {
    trackAddBankSuccess({
      bankAccountType: this.bankAccountType,
      routingNumber: this.routingNumber.slice(-4), // Last 4 digits only
      accountNumber: this.accountNumber.slice(-4)  // Last 4 digits only
    });

    // If no errors, emit the success event with the payment instrument token
    const paymentInstrumentToken = response.data.paymentInstrument?.paymentInstrumentToken;
    this.devLog('Emitting bank account payment instrument token:', paymentInstrumentToken);
    this.devLog('Full response:', response);
    this.bankAccountAdded.emit(paymentInstrumentToken);
    this.resetForm();
  }

  private handleResponseError(response: any) {
        // Track bank account addition error
        const errorMessage = response.message || 'Bank Account addition failed';
        trackAddBankError(errorMessage);

        // Capture API errors and display them in the UI
        if (response.errors) {
          // Track API validation errors
          trackValidationError('bank', response.errors);

          this.apiErrors = response.errors.reduce((acc: { [key: string]: string[] }, error: string) => {
            acc.General = acc.General || [];
            acc.General.push(error);
            return acc;
          }, {});
        } else if (response.message) {
          // Track single API validation error
          trackValidationError('bank', [response.message]);

          this.apiErrors = { General: [response.message] };
        }
  }

  private applySuggested(countryCode: string, stateCode: string) {
    const selectedCountry = COUNTRY.find(country => country.code === countryCode);

    if (selectedCountry) {
      this.country = selectedCountry.code;
      this.errors = { ...this.errors, country: '' };
      this.state = '';
    } else {
      this.country = '';
      this.errors = { ...this.errors, country: '' };
      this.state = '';
    }

    if (this.isCountryUSorCanada()) {
      const stateExists = STATES(this.country).some(s => s.code === stateCode);
      if (stateExists)
        this.state = stateCode;
    }
    this.devLog('Applied suggested address:', { countryCode, stateCode });
  }
  private handleUnknownError(response: any) {
    // Set default message if no broken rules exist
    if (!response?.data?.BrokenRules?.length) {
      response.data = {
        ...response.data,
        BrokenRules: ['Payment Method addition failed']
      };
    }

    let errorMessage = response.data.BrokenRules[0];
    this.brokenRules = response.data.BrokenRules;
    this.showErrorBanner(this.brokenRules);

    trackAddBankError('Unknown Error: ' + errorMessage);
    this.addressValidation.emit(errorMessage);
    this.devLog('Event emitted:', {
      message: errorMessage,
      UnKnown: AccountStatus[response?.data?.AccountStatus]
    });
  }

  private handleAddressValidation(response: any) {
    this.address1 = response.data.SuggestedAddresses[0].AddressLine1;
    this.address2 = response.data.SuggestedAddresses[0].AddressLine2;
    this.city = response.data.SuggestedAddresses[0].City;
    this.zipCode = response.data.SuggestedAddresses[0].PostalCode;
    this.applySuggested(
      response.data.SuggestedAddresses[0].CountryCode,
      response.data.SuggestedAddresses[0].ProvinceOrStateCode
    );

    this.isAddressResubmission = true;
    const addressUpdatedMessage = response?.data?.BrokenRules?.[0] ||
      "Address updated. Please review and confirm the changes before submitting.";

    this.addressValidation.emit(addressUpdatedMessage);
    this.devLog('Address updated event emitted:', {
      message: addressUpdatedMessage,
      AddressValidation: AccountStatus[AccountStatus.AddressSuggested]
    });
  }

  private handleValidationError(response: any) {
    let errorMessage = response?.data?.BrokenRules?.[0];
    if (response?.data?.AccountStatus === AccountStatus.AddressValidationError) {
      errorMessage = errorMessage || "Address provided could not be validated. Please update the provided address and resubmit.";
    } else if (response?.data?.AccountStatus === AccountStatus.AddressValidationUnknownStatus) {
      errorMessage = errorMessage || "Address could not be validated. Please update and resubmit.";
    } else {
      errorMessage = errorMessage || "Payment method addition failed.";
    }
    this.isAddressResubmission = true;
    trackAddBankError('Address validation error: ' + errorMessage);
    this.addressValidation.emit(errorMessage);
    this.devLog('addressValidation event emitted:', {
      message: errorMessage,
      AddressValidation: AccountStatus[response?.data?.AccountStatus]
    });
  }
  resetForm() {
    this.accountHolderName = '';
    this.accountNumber = '';
    this.routingNumber = '';
    this.bankAccountType = BankAccountType.Checking;
    this.firstName = '';
    this.lastName = '';
    this.address1 = '';
    this.address2 = '';
    this.city = '';
    this.state = '';
    this.country = 'US';
    this.zipCode = '';
    this.address2 = '';
    this.dateofBirth = '';
    this.paymentAccountNickname = '';
    this.mobilePhoneNumber = '';
    this.emailAddress = '';
    this.selectedPhoneCode = '';
    this.errors = {
      accountHolderName: '',
      accountNumber: '',
      routingNumber: '',
      bankAccountType: '',
      firstName: '',
      lastName: '',
      address1: '',
      city: '',
      state: '',
      country: '',
      zipCode: '',
      address2: '',
      dateofBirth: '',
      paymentAccountNickname: '',
      mobilePhoneNumber: '',
      emailAddress: '',
      selectedPhoneCode: '',
    };
  }

  goToWallet = () => {
    // Track bank account addition cancellation
    trackAddBankCancelled();

    this.goToPaymentSelector.emit(true);
  };
  private handleCloseBanner = () => {
    this.brokenRules = [];
  };
  private renderErrorBanner() {
    if (!this.brokenRules.length) return null;
    return (
      <div ref={el => this.errorBannerRef = el} class={`${this.isAddressSuggestion ? 'addressVerification-message-warning-info' : 'addressVerification-message-error-info'}`}>
        <div class="addressVerification-message-notice-body">
          <div class="addressVerification-message-warning-content">
            {this.brokenRules[0]}
          </div>
        </div>
        <button
          class="addressVerification-close-btn"
          type="button"
          aria-label="Close"
          onClick={this.handleCloseBanner}
        >
          ×
        </button>
      </div>
    );
  }
  private renderAccountFields() {
    return (
      <div class="wallet-row">
        <div class="wallet-col-md-6 wallet-col-lg-3">
          <label htmlFor="account-holder-name" class={this.errors.accountHolderName ? 'validation-error' : ''}>Account Holder Name <span class="required-asterisk">*</span></label>
          <input
            type="text"
            id="account-holder-name"
            autoComplete="cc-given-name"
            placeholder="Enter Account Holder Name"
            value={this.accountHolderName}
            onInput={(event) => this.handleInputChange('accountHolderName', (event.target as HTMLInputElement).value)}
          />
          {this.errors.accountHolderName && <span class="error">{this.errors.accountHolderName}</span>}
        </div>

        <div class="wallet-col-md-6 wallet-col-lg-3">
          <label htmlFor="account-number" class={this.errors.accountNumber ? 'validation-error' : ''}>Account Number <span class="required-asterisk">*</span></label>
          <input
            type="text"
            id="account-number"
            placeholder="Enter Account Number"
            value={this.accountNumber}
            onInput={(event) => this.handleInputChange('accountNumber', (event.target as HTMLInputElement).value)}
          />
          {this.errors.accountNumber && <span class="error">{this.errors.accountNumber}</span>}
        </div>

        <div class="wallet-col-md-6 wallet-col-lg-3">
          <label htmlFor="routing-number" class={this.errors.routingNumber ? 'validation-error' : ''}>Routing Number <span class="required-asterisk">*</span></label>
          <input
            type="text"
            id="routing-number"
            placeholder="Enter Routing Number"
            value={this.routingNumber}
            onInput={(event) => this.handleInputChange('routingNumber', (event.target as HTMLInputElement).value)}
          />
          {this.errors.routingNumber && <span class="error">{this.errors.routingNumber}</span>}
        </div>

        <div class="wallet-col-md-6 wallet-col-lg-3">
          <label htmlFor="account-type" class={this.errors.bankAccountType ? 'validation-error' : ''}>Account Type </label>
          <div class="bank-account-type" role="radiogroup" aria-labelledby="account-type">
            <label class="custom-radio">
              <input type="radio" name="account-type" checked={this.bankAccountType === BankAccountType.Checking} onChange={() => this.handleInputChange('bankAccountType', BankAccountType.Checking)} />{' '}
              Checking
            </label>
            <label class="custom-radio">
              <input type="radio" name="account-type" checked={this.bankAccountType === BankAccountType.Savings} onChange={() => this.handleInputChange('bankAccountType', BankAccountType.Savings)} />{' '}
              Savings
            </label>
          </div>
          {this.errors.bankAccountType && <span class="error">{this.errors.bankAccountType}</span>}
        </div>
      </div>
    );
  }

  private renderPersonalInfo() {
    return (
      <div class="wallet-row">
        <div class="wallet-col-md-6">
          <label htmlFor="first-name" class={this.errors.firstName ? 'validation-error' : ''}>First Name <span class="required-asterisk">*</span></label>
          <input
            type="text"
            id="first-name"
            autoComplete="cc-given-name"
            placeholder="Enter First Name"
            value={this.firstName}
            onInput={(event) => this.handleInputChange('firstName', (event.target as HTMLInputElement).value)}
          />
          {this.errors.firstName && <span class="error">{this.errors.firstName}</span>}
        </div>
        <div class="wallet-col-md-6">
          <label htmlFor="last-name" class={this.errors.lastName ? 'validation-error' : ''}>Last Name <span class="required-asterisk">*</span></label>
          <input
            type="text"
            id="last-name"
            autoComplete="cc-family-name"
            placeholder="Enter Last Name"
            value={this.lastName}
            onInput={(event) => this.handleInputChange('lastName', (event.target as HTMLInputElement).value)}
          />
          {this.errors.lastName && <span class="error">{this.errors.lastName}</span>}
        </div>
                <div class="wallet-col-md-6">
          <label htmlFor="Nick-name" class={this.errors.paymentAccountNickname ? 'validation-error' : ''}>Payment Account Nickname <span class="required-asterisk">*</span></label>
          <input
            type="text"
            id="Nick-name"
            placeholder="Enter Payment Account Nickname"
            value={this.paymentAccountNickname}
            onInput={(event) => this.handleInputChange('paymentAccountNickname', (event.target as HTMLInputElement).value)}
          />
          {this.errors.paymentAccountNickname && <span class="error">{this.errors.paymentAccountNickname}</span>}
        </div>
      </div>
    );
  }

  private renderAddressSection() {
    return (
      <>
        <div class="wallet-row">
          <div class="wallet-col-md-6 wallet-col-lg-4">
            <label htmlFor="address1" class={this.errors.address1 ? 'validation-error' : ''}>Address Line 1 <span class="required-asterisk">*</span></label>
            <input
              type="text"
              id="address1"
              autoComplete="address-line1"
              placeholder="Enter Address Line 1"
              value={this.address1}
              onInput={(event) => this.handleInputChange('address1', (event.target as HTMLInputElement).value)}
            />
            {this.errors.address1 && <span class="error">{this.errors.address1}</span>}
          </div>
          <div class="wallet-col-md-6 wallet-col-lg-4">
            <label htmlFor="address2">Address Line 2 (Optional)</label>
            <input
              type="text"
              id="address2"
              autoComplete="address-line2"
              placeholder="Apt, suite, unit, etc. (optional)"
              value={this.address2}
              onInput={(event) => this.handleInputChange('address2', (event.target as HTMLInputElement).value)}
            />
            {this.errors.address2 && <span class="error">{this.errors.address2}</span>}
          </div>
          <div class="wallet-col-md-6 wallet-col-lg-4">
            <label htmlFor="city" class={this.errors.city ? 'validation-error' : ''}>City <span class="required-asterisk">*</span></label>
            <input
              type="text"
              id="city"
              autoComplete="address-level2"
              placeholder="Enter City"
              value={this.city}
              onInput={(event) => this.handleInputChange('city', (event.target as HTMLInputElement).value)}
            />
            {this.errors.city && <span class="error">{this.errors.city}</span>}
          </div>
        </div>
        <div class="wallet-row">
          <div class={`wallet-col-md-6 ${this.isCountryUS() ? 'wallet-col-lg-4' : ''}`}>
            <label htmlFor="country" class={this.errors.country ? 'validation-error' : ''}>Country <span class="required-asterisk">*</span></label>
            <select
              id="country"
              autoComplete="country"
              onInput={(event) => this.handleCountryChange(event)}
            >
              <option value="">Select Country</option>
              {COUNTRY.map(country => (
                <option key={country.code} value={country.code}>
                  {country.name}
                </option>
              ))}
            </select>
            {this.errors.country && <span class="error">{this.errors.country}</span>}
          </div>
          {(this.country === 'US' || this.country === 'CA') && (
            <div class="wallet-col-md-6 wallet-col-lg-4">
              <label htmlFor="state" class={this.errors.state ? 'validation-error' : ''}>State/Province <span class="required-asterisk">*</span></label>
              <select
                id="state"
                autoComplete="address-level1"
                onInput={(event) => this.handleStateChange(event)}
              >
                <option value="">Select {this.country === 'US' ? 'State' : 'Province'}</option>
                {STATES(this.country).map(state => (
                  <option key={state.code} value={state.code}>
                    {state.name}
                  </option>
                ))}
              </select>
              {this.errors.state && <span class="error">{this.errors.state}</span>}
            </div>
          )}
          <div class={`wallet-col-md-6 ${this.isCountryUS() ? 'wallet-col-lg-4' : ''}`}>
            <label htmlFor="zip-code" class={this.errors.zipCode ? 'validation-error' : ''}>ZIP/Postal Code <span class="required-asterisk">*</span></label>
            <input
              type="text"
              id="zip-code"
              autoComplete="postal-code"
              placeholder="Enter ZIP/Postal Code"
              value={this.zipCode}
              onInput={(event) => this.handleInputChange('zipCode', (event.target as HTMLInputElement).value)}
            />
            {this.errors.zipCode && <span class="error">{this.errors.zipCode}</span>}
          </div>
          <div class="wallet-col-md-6">
            <label htmlFor="emailAddress" class={this.errors.emailAddress ? 'validation-error' : ''}>E-Mail Address <span class="required-asterisk">*</span></label>
            <input
              type="text"
              id="emailAddress"
              placeholder="Enter E-Mail Address"
              value={this.emailAddress}
              onInput={event => this.handleInputChange('emailAddress', (event.target as HTMLInputElement).value)}
            />
            {this.errors.emailAddress && <span class="error">{this.errors.emailAddress}</span>}
          </div>
          <div class="wallet-col-md-6">
            <label htmlFor="mobilePhoneNumber" class={this.errors.mobilePhoneNumber ? 'validation-error' : ''}>
              Mobile Phone Number <span class="required-asterisk">*</span>
            </label>

            {this.country === 'US' ? (
              <div class="field-stack single-field">
                <div class="input-wrapper">
                  <input
                    type="text"
                    id="mobilePhoneNumber"
                    placeholder="Enter Mobile Phone Number"
                    maxLength={14}
                    value={this.mobilePhoneNumber}
                    onInput={event => this.handleMobileNumberChange(event)}
                  />
                  {this.errors.mobilePhoneNumber && <span class="error">{this.errors.mobilePhoneNumber}</span>}
                </div>
              </div>
            ) : (
              <div class="field-stack dual-field">
                <div class="input-wrapper">
                  <div class="dropdown-wrapper" ref={el => (this.dropdownWrapper = el as HTMLElement)}>
                    <button type="button" class="dropdown-header" onClick={this.togglePhoneCodeDropdown} aria-haspopup="listbox" aria-expanded={this.isPhoneCodeDropdownOpen}>
                      +{this.selectedPhoneCode || ' '}
                    </button>
                    {this.isPhoneCodeDropdownOpen && (
                      <ul class="dropdown-list">
                        {CountryAndPhoneCodes.map(country => (
                          <li class="dropdown-item">
                            <button type="button" class="dropdown-button" onClick={() => this.selectPhoneCode(country.PhoneCode)}>
                              <div class="dropdown-content">
                                <span>{country.Description?.toLowerCase()}</span>
                                <span>+{country.PhoneCode}</span>
                              </div>
                            </button>
                          </li>
                        ))}
                      </ul>
                    )}
                  </div>
                  {this.errors.selectedPhoneCode && <span class="error">{this.errors.selectedPhoneCode}</span>}
                </div>
                <div class="input-wrapper">
                  <input
                    type="text"
                    id="mobilePhoneNumber"
                    placeholder="Enter Mobile Phone Number"
                    maxLength={20}
                    value={this.mobilePhoneNumber}
                    onInput={event => this.handleMobileNumberChange(event)}
                  />
                  {this.errors.mobilePhoneNumber && <span class="error">{this.errors.mobilePhoneNumber}</span>}
                </div>
              </div>
            )}
          </div>
          <div class={`wallet-col-md-6 ${this.isCountryUS() ? 'wallet-col-lg-4' : ''}`}>
            <label htmlFor="Date-of-Birth" class={this.errors.dateofBirth ? 'validation-error' : ''}>Date of Birth <span class="required-asterisk">*</span></label>
           <date-picker
              
              id="Date-of-Birth" 
              placeholder="MM/DD/YYYY"
              minAge={18}
              value={this.dateofBirth}
              onDateChange={(e: CustomEvent<string>) => this.handleBirthDateChange(e.detail)}
            />
            {this.errors.dateofBirth && <span class="error">{this.errors.dateofBirth}</span>}
          </div>
        </div>
      </>
    );
  }

  private renderApiErrors() {
    if (!Object.keys(this.apiErrors).length) return null;

    return (
      <div class="api-errors">
        <div class="error-content">
          <h5>Error Message:</h5>
          <ul>
            {Object.entries(this.apiErrors).map(([field, messages]) =>
              messages.map((msg, idx) => (
                <li key={`${field}-${idx}`}>
                  <strong>{field === 'General' ? '' : `${field}: `}</strong>
                  {msg}
                </li>
              ))
            )}
          </ul>
        </div>
      </div>
    );
  }

  private renderFormActions() {
    return (
      <div class="wallet-col-12 footer">
        <md-filled-button class="button button-secondary" onClick={this.goToWallet}>Cancel</md-filled-button>
        {this.isLoading ? (
          <md-filled-button class="button button-primary" type="button">
            <span>Adding Account&nbsp;<span class="dot-flashing"></span></span>
          </md-filled-button>
        ) : (
          <md-filled-button
            class="button button-primary"
            disabled={this.isVerifying}
            data-loading={this.isVerifying ? 'true' : 'false'}
          >
            {this.isVerifying ? 'Verifying...' : 'Add Account'}
          </md-filled-button>
        )}
      </div>
    );
  }

  render() {
    return (
      <div class="add-bank-account">
        <h3>Add Bank Account</h3>
        {this.mfaPopUp && (
          <mfa-model-popup 
           operationsToken = {this.operationsToken}
           userScopedAccessToken={this.userScopedAccessToken}
            environment={this.environment}
            addRequestPayload={this.PaymentAccountRequestPayload}
            requestType="bank" 
            initialMfaResponse={this.mfaResponsePayload} 
            onClose={this.handleClosePopup}
            errorHandler={(messages) => this.handleMfaError(messages)}
            onSuccessEvent={(event) => this.handleSuccess(event.detail)}
            />
        )}
        <form onSubmit={(event) => this.handleSubmit(event)}>
          {this.renderErrorBanner()}
          {this.renderAccountFields()}
          <h4>Account Holder Information</h4>
          {this.renderPersonalInfo()}
          <h4>Billing Address</h4>
          {this.renderAddressSection()}
          {this.renderApiErrors()}
          {this.renderFormActions()}
        </form>
      </div>
    );
  }
}
